.header{
    background-color: $mamey;

    .contenido-header {
        padding: $separacion / 4 0;
        color: $blanco;

        
          
        @include tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
            font-size: 3rem;
            font-weight: $regular4;
        }
    
    }

    h1 {
        margin-bottom: 1rem;
        text-align: center;
        display: flex;
        flex-direction: column;
        font-size: 3rem;

        @include tablet {
            margin-bottom: 0;
        }
        
    }
    
    spam {
        font-size: 2rem;
        font-weight: $regular3;
    }
}

.navegacion-principal {
    display: flex;
    gap: 2rem;
    justify-content: center;

    a {
        color: $blanco;
        font-size: 1.5rem;

        &:hover {
            color: $naranja;
        }
    }
}